import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { CustomerListComponent } from './customerlist.component';
import { CustomerAddComponent } from './customer-add.component';
import { LayoutModule } from '../../../layouts/layout.module';
import { DefaultComponent } from '../../default.component';
import {HttpClientModule} from '@angular/common/http';
import { HTTP_INTERCEPTORS } from '@angular/common/http';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {CdkTableModule} from '@angular/cdk/table';
import {ListModule} from '../../list/list.module';
import {ListComponent, SafeHtmlPipe} from '../../list/list.component';
import { SmartSearchModule } from '../../../layouts/smart-search/smart-search.module';
import {ToastModule} from '../../../layouts/toast-notification/toast.module';
import {
	  MatAutocompleteModule,
	  MatButtonModule,
	  MatButtonToggleModule,
	  MatCardModule,
	  MatCheckboxModule,
	  MatChipsModule,
	  MatDatepickerModule,
	  MatDialogModule,
	  MatExpansionModule,
	  MatGridListModule,
	  MatIconModule,
	  MatInputModule,
	  MatListModule,
	  MatMenuModule,
	  MatNativeDateModule,
	  MatPaginatorModule,
	  MatProgressBarModule,
	  MatProgressSpinnerModule,
	  MatRadioModule,
	  MatRippleModule,
	  MatSelectModule,
	  MatSidenavModule,
	  MatSliderModule,
	  MatSlideToggleModule,
	  MatSnackBarModule,
	  MatSortModule,
	  MatTableModule,
	  MatTabsModule,
	  MatToolbarModule,
	  MatTooltipModule,
	  MatStepperModule,
	} from '@angular/material';
	
const routes: Routes = [
    {
        "path": "",
        "component": DefaultComponent,
        "children": [
            {
                "path": "",
                "component": CustomerListComponent
            }
        ]
    },{
        "path": "customer-add",
        "component": DefaultComponent,
        "children": [
            {
                "path": "",
                "component": CustomerAddComponent,
            }
        ]
    },
];

@NgModule({
    imports: [
        CommonModule, RouterModule.forChild(routes), LayoutModule, HttpClientModule, 
        FormsModule, ReactiveFormsModule, ListModule, ToastModule,
		SmartSearchModule,
       MatInputModule, MatNativeDateModule, MatCardModule, MatSelectModule, MatSidenavModule, MatTableModule, MatSortModule, MatDatepickerModule, MatCheckboxModule, MatRadioModule, MatPaginatorModule,
    ], 
	exports: [ RouterModule ], 
	declarations: [ CustomerListComponent, CustomerAddComponent ],
})
export class CustomerListModule { }





