import { Component, OnInit, ViewEncapsulation, AfterViewInit, AfterViewChecked, ElementRef, ViewChild, HostBinding, Renderer2, Directive} from '@angular/core';
import { Helpers } from '../../../../helpers';
import { ScriptLoaderService } from '../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../animations/round-anticlock.animation';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Observable} from 'rxjs/Observable';
import {FormControl, Validators, FormGroup, ValidationErrors} from '@angular/forms';
import {ListComponent, SafeHtmlPipe} from '../../list/list.component'
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute} from '@angular/router';
import { SmartSearchComponent } from '../../../layouts/smart-search/smart-search.component';

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./customerlist.component.html",
    styleUrls: ['./customerlist.component.css'],
    encapsulation: ViewEncapsulation.None,
	animations: [ 
		FLY_IN_OUT_ANIMATION,
    ROUND_ANTICLOCK_ANIMATION
  ],
})
export class CustomerListComponent implements AfterViewInit {
	 @ViewChild(SmartSearchComponent)smart: SmartSearchComponent;
	@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
	@ViewChild(ListComponent) list: ListComponent;
	
	configCustomerName = "";
	smartHide: boolean = true;
	animateSmart: string = 'out';
	customerId = 0;  
	tag: string  ="";
	
	
	  
    constructor(private _script: ScriptLoaderService, 	private http: HttpClient,
			    private route:ActivatedRoute ,private router: Router) {
		this.route.queryParams.filter(params => params.tag).subscribe(params=>{
			if(params.tag != undefined){
				this.tag =params.tag;
				if(this.tag == 'Supplier' || this.tag == 'supplier'){
					this.configCustomerName ="Supplier";
				}else{
					this.configCustomerName = sessionStorage.getItem('configCustomerName');
				}
				
			}
			
		});  
	}
	  
	    
    ngOnInit() {
		this.list.listParam['tag'] = this.tag;
		this.smart.smartUrl = sessionStorage.getItem('requestUrl')+"/axelacrm/customer/customer-smartsearch";
  		this.list.listUrl = sessionStorage.getItem('requestUrl')+"/axelacrm/customer/customer-list";
	}
	  
	  ngAfterViewInit(){
		
	}
	
	 smartToggle(){
		  if(this.smartHide){
			  this.animateSmart = 'in';
			  this.smartHide = false;
		  }else{
			  this.animateSmart='out';
			  this.smartHide = true;
		  }
	  }
	  
	toggleAnimation(){
		   this.router.navigate(['customer/customerlist/customer-add'], {queryParams : { customerId : this.customerId, tag: this.tag}});
		
	  }
	
	  smartClickListener(event){
		  this.list.listParam['requestType']='filter';
		  this.list.listParam['smartParam']=event;
		 this.list.getData();
	 }
	  
	listClickListener(listParam: any[]){
		if(listParam[0] == 'Update Customer'){
			this.customerId = listParam[1];
			this.toggleAnimation();
		}
		if(listParam[0] == 'Update Supplier'){
			this.customerId = listParam[1];
			this.toggleAnimation();
		}
	}
	 }



